.Payment {
  width: 100%;
  height: 100vh;
  font-family: Roboto;
}

.Payments_Main {
  width: 75%;
  margin: 5% auto;
}

.Payments_Main ul li {
  padding: 14px 0 20px 0;
  margin-left: 3%;
  list-style: none;
  position: relative;
}

.Payments_Main ul li a {
  color: #566171;
  text-decoration: none;
}

.Payments_Main ul li a:hover {
  text-decoration: underline;
}

.Payments_Main ul li:after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 90%;
  height: 1px;
  opacity: 0.12;
  background-color: #333846;
}
